html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media only screen and (max-width: 1024px) {
  .card {
    flex-direction: column;
  }
  .card__left {
    order: 1;
  }
  .footer {
    padding: 0 80px;
  }
  .whom {
    padding: 0 100px;
  }
  .whom__answer {
    font-size: 30px;
  }
  .footer h2 {
    font-size: 24px;
  }
  .footer__collage {
    max-width: 850px;
  }
  .footer__button {
    max-width: 850px;
  }
  .footer__know {
    font-size: 24px;
  }
  .footer__com {
    font-size: 24px;
  }
}

@media only screen and (max-width: 860px) {
  .footer {
    margin-left: 80px;
  }
  .footer__collage {
    max-width: 640px;
  }
  .footer__button {
    max-width: 640px;
    font-size: 27px;
  }
}

@media only screen and (max-width: 640px) {
  .footer__collage {
    max-width: 570px;
  }
  .footer__button {
    max-width: 570px;
    font-size: 27px;
  }
}

@media only screen and (max-width: 480px) {
  .footer__collage {
    max-width: 360px;
  }
  .footer__button {
    max-width: 360px;
  }
}

@font-face {
  font-family: 'Circe';
  src: url(../fonts/Circe-Bold.ttf);
}

@font-face {
  font-family: 'PT Sans';
  src: url(../fonts/pt-sans.ttf);
}

@font-face {
  font-family: 'PT Sans bold';
  src: url(../fonts/pt-sans-bold.ttf);
}

@font-face {
  font-family: 'Georgia Italic';
  src: url(../fonts/GeorgiaItalic.ttf);
}

.block {
  margin: 0 auto;
  max-width: 1000px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 28px;
}

.header__logo h4 {
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 27px;
  color: #18374b;
  margin-left: 20px;
  margin-top: 2px;
}

.free-veb {
  border-top: 1px solid #000000;
  text-align: center;
}

.free-veb h1 {
  font-family: "Circe", sans-serif;
  font-weight: 700;
  font-size: 34px;
  text-transform: uppercase;
  color: #ff621f;
  margin-top: 45px;
}

.free-veb h3 {
  font-family: "Circe", sans-serif;
  font-weight: 700;
  font-size: 30px;
  text-transform: uppercase;
  color: #020001;
  margin-top: 29px;
  margin-bottom: 20px;
  line-height: 1.25;
}

.free-veb__button {
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 30px;
  text-decoration: none;
  padding: 16px 38px 15px 68px;
  background: #ff621f;
  color: #fff;
  border-bottom: 7px solid #f04941;
}

.card {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin-top: 40px;
  max-width: 1000px;
}

.card__left {
  background: #ff621f;
  max-width: 500px;
  padding-bottom: 145px;
  height: 1276px;
}

.card__left-date {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin-left: 20px;
}

.card__left-date img {
  margin-top: 36px;
}

.card__left-day {
  color: #fff;
  text-transform: uppercase;
  margin-left: 34px;
  margin-top: 28px;
}

.day__week {
  font-family: "Circe", sans-serif;
  font-weight: 700;
  font-size: 30px;
}

.day__month {
  font-family: "Circe", sans-serif;
  font-weight: 700;
  font-size: 36px;
  margin-top: -8px;
}

.day__time {
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 24px;
  border-top: 3px dotted #fff;
  padding-top: 17px;
  width: 294px;
}

.card__left-spiker {
  text-align: center;
  margin-top: 50px;
}

.card__left-spiker p {
  margin-top: 20px;
  margin-bottom: 14px;
  color: #fff;
  font-family: "Georgia Italic", sans-serif;
  font-weight: 400;
  font-size: 22px;
}

.card__left-spiker h2 {
  text-transform: uppercase;
  color: #fff;
  font-family: "PT Sans bold", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 1.2;
}

.card__left-list {
  color: #fff;
  margin-top: 26px;
  margin-left: 84px;
  margin-right: 15px;
}

.card__left-list li {
  padding: 9px 0px 9px 2px;
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 18px;
  margin-bottom: 2px;
  letter-spacing: 0.003em;
}

.card__right {
  background: #eaeaea;
  color: #2b2e35;
  max-width: 500px;
  padding-left: 10px;
  height: 1276px;
}

.card__start {
  font-family: "PT Sans bold", sans-serif;
  font-weight: 700;
  font-size: 21px;
  text-transform: uppercase;
  text-align: center;
  margin-top: 30px;
}

.card__info {
  font-family: "PT Sans bold", sans-serif;
  font-weight: 700;
  font-size: 24px;
  text-align: start;
  font-size: 24px;
  text-transform: uppercase;
  margin-top: 27px;
  margin-left: 26px;
}

.card__right-list {
  margin-top: 62px;
  margin-left: 32px;
  margin-right: 30px;
  margin-bottom: 55px;
}

.card__right-list span {
  font-family: "PT Sans bold", sans-serif;
  font-weight: 700;
  font-size: 24px;
}

.card__right-list li:before {
  position: absolute;
  content: url("../img/correct.png");
  margin-left: -30px;
  padding-right: 20px;
}

.card__right-list li {
  list-style-type: none;
  padding-left: 34px;
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.2;
  color: #2b2e35;
  margin-bottom: 30px;
}

.timer {
  display: flex;
  justify-content: center;
  margin-top: 14px;
  margin-left: -12px;
}

.timer__time div {
  font-family: "Circe", sans-serif;
  font-weight: 700;
  font-size: 48px;
  color: #fff;
  background: #2b2e35;
  border-radius: 10px;
  width: 68px;
  height: 68px;
  text-align: center;
  margin: 0 4px;
}

.timer__time p {
  font-family: "Circe", sans-serif;
  font-weight: 700;
  font-size: 14px;
  text-align: center;
  margin-top: 8px;
}

.whom {
  margin-top: 48px;
  text-align: center;
  padding-bottom: 43px;
}

.whom__format {
  font-family: "Georgia Italic", sans-serif;
  font-weight: 400;
  font-size: 24px;
}

.whom__format span {
  font-size: 30px;
}

.whom__button {
  margin-top: 48px;
  margin-left: -10px;
  font-family: "PT Sans bold", sans-serif;
  font-weight: 700;
  font-size: 36px;
  color: #fff;
  background: #ff621f;
  padding: 5px 40px 7px 42px;
}

.whom__answer {
  margin-top: 20px;
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 36px;
  color: #2b2e35;
  padding-bottom: 34px;
  border-bottom: 2px dotted #2b2e35;
  line-height: 1.2;
}

.f-block {
  background: url("../img/background0.png") no-repeat;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
}

.footer {
  max-width: 1004px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
}

.footer h2 {
  font-family: "PT Sans bold", sans-serif;
  font-weight: 700;
  font-size: 36px;
  text-align: center;
  padding-top: 9px;
  line-height: 1.24;
  padding-bottom: 24px;
}

.footer p {
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 24px;
  padding-bottom: 29px;
  line-height: 1.3em;
}

.footer p span {
  font-family: "PT Sans bold", sans-serif;
  font-weight: 700;
  font-size: 27px;
}

.footer__business {
  font-size: 27px !important;
}

.footer__collage {
  border: 6px solid #ff621f;
  margin-top: 12px;
  max-width: 1000px;
  margin-bottom: 22px;
}

.footer__know {
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 36px;
}

.footer__com {
  font-family: "PT Sans bold", sans-serif;
  font-weight: 700;
  font-size: 30px;
  color: #2b2e35;
  margin-top: 55px;
}

.footer__button {
  width: 1000px;
  height: 108px;
  background: #ff621f;
  color: #fff;
  border-bottom: 12px solid #f04941;
  font-family: "PT Sans bold", sans-serif;
  font-weight: 700;
  font-size: 48px;
  margin-top: 85px;
}

.social {
  margin-top: 43px;
  margin-bottom: 54px;
}

.social img {
  width: 42px;
  height: 42px;
}
